@import "../styles/variables.scss";
@import "../styles/elements.scss";

.topbar {
	animation: fade 0.4s ease-in;

	& > .link {
		display: inline-block;
		margin: 10px 25px;
		color: $text-color-red;
		cursor: pointer;
		transition: ease-in-out 0.3s;

		&:hover {
			transition: ease-in-out 0.3s;
			color: $text-color-lightred;
		}

		&:focus,
		&:active {
			color: $text-color-darkred;
		}
	}
}
