@import "../styles/variables.scss";
@import "../styles/elements.scss";

.aboutme {
	grid-template-columns: min-content 1fr;
	grid-template-rows: repeat(3, min-content);

	align-items: center;

	& > img,
	& > p {
		grid-row: span 1;
		grid-column: span 1;
	}

	& > img {
		width: 100px;
		height: 100px;
		border-radius: 50%;

		transition: ease-in-out 0.3s;
		filter: grayscale(100%);

		&:hover {
			transition: ease-in-out 0.3s;
			filter: grayscale(0%);
		}
	}

	& > p {
		vertical-align: middle;
	}

	& > .contact {
		grid-row: span 1;
		grid-column: span 2;
		text-align: right;
	}
}
